<template>
  <!--法律咨询-->
  <div>
    <Header></Header>
    <div class="row">
      <div class="flex-shrink-0 p-3 bg-white col-4" style="width: 280px;min-height: 620px;">
        <a-menu
            style="width: 256px"
            :default-selected-keys="[]"
            :open-keys.sync="openKeys"
            mode="inline"
            @click="handleClick"
        >
          <a-sub-menu key="sub1" @titleClick="titleClick">
            <span slot="title"><a-icon type="question-circle" /><span>日常法律咨询</span></span>
              <a-menu-item key="1">
                发起咨询
              </a-menu-item>
              <a-menu-item key="2">
                我的咨询
              </a-menu-item>
          </a-sub-menu>
          <a-sub-menu key="sub2" @titleClick="titleClick">
            <span slot="title"><a-icon type="solution" /><span>乡村创业法律咨询</span></span>
            <a-menu-item key="3">
              发起咨询
            </a-menu-item>
            <a-menu-item key="4">
              我的咨询
            </a-menu-item>
            <a-menu-item key="5">
              政策扶持
            </a-menu-item>
            <a-menu-item key="6">
              风采展示
            </a-menu-item>
            <a-menu-item key="7">
              创业一点通
            </a-menu-item>
          </a-sub-menu>
        </a-menu>
      </div>
      <div class="col-8">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
  import Header from "@/components/Header";
  import Footer from "@/components/Footer";

  export default {
    name: 'LegalAdvice',
    components: {
      Footer,
      Header,
    },
    data() {
      return {
        openKeys: ['sub1', 'sub2'],
      };
    },
    watch: {
      openKeys(val) {
        console.log('openKeys', val);
      },
    },
    methods: {
      handleClick(e) {
        
        const h = this.$createElement;
        
        // console.log('click', e);
        switch (e.key) {
          case '1':
            this.$router.push({
              name: 'dailyConsultation'
            })
            break
          case '2':
            this.$router.push({
              name: 'myConsultation'
            })
            break
          case '3':
            this.$router.push({
              name: 'consultation'
            })
            break
          case '4':
            this.$router.push({
              name: 'myConsultation'
            })
            break
          case '5':
            this.$info({
              title: '功能正在开发中...',
              content: h('div', {}, [
              ]),
              onOk() {},
            })
            break
          case '6':
            this.$info({
              title: '功能正在开发中...',
              content: h('div', {}, [
              ]),
              onOk() {},
            })
            break
          case '7':
            this.$info({
              title: '功能正在开发中...',
              content: h('div', {}, [
              ]),
              onOk() {},
            })
            break
        }
      },
      titleClick(e) {
        console.log('titleClick', e);
      },
    },
  }
</script>

<style scoped>
  .row {
    margin-top: 6%;
  }

  .col-8 {
    margin-top: 1%;
  }
</style>